let passwordFields = document.querySelectorAll("._pass-field")

if(passwordFields){
    passwordFields.forEach(field => {
        let input = field.querySelector("input")
        let toggler = field.querySelector(".pass-toggle")

        toggler.onclick = function () {
            field.classList.toggle("_show")

            if(field.classList.contains("_show")){
                input.setAttribute("type","text")
            } else{
                input.setAttribute("type","password")
            }
        }
    })
}



